body {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  height: 100vh;
  background:url('./background/bg.jpeg') center;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover
}
.App {
  color: whitesmoke;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  border-radius: 5%;
  width: 220px;
  text-shadow: 2px 2px 4px #000000;
  height: 370px;
  /* background-color: #52B3D9; */
  -webkit-box-shadow: 9px 10px 20px 0px rgba(0, 0, 0, 0.75);
  box-shadow: 9px 10px 20px 0px rgba(0, 0, 0, 0.75);
  font-size: 9pt;
  background: url('./background/widgetBackground.jpeg') center;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover
}

h1 {
  padding-left: 0.8rem;
}

.time {
  text-align: right;
  padding-right: 1rem;
  font-size: 15pt;
}

.form span:first-of-type {
  padding-left: 7rem;
}

.weatherConditions p{
  margin-left: 0.5rem;
  font-size: 12pt;
}

.weatherConditions p:nth-of-type(2n) {
  margin-left: 1rem;
}

.weatherConditions p:nth-of-type(3n) {
  margin-left: 5rem;
}

.temp {
  font-size: 14pt;
  color: tomato;
}

.cityName {
  color: tomato;
  font-size: 18pt;
}

span {
  margin-left: 0.7rem;
}

.rtl {
  display: block;
}

input:first-of-type {
  margin-left: 0rem;
}

input[type=text],
textarea {
  -webkit-transition: all 0.30s ease-in-out;
  -moz-transition: all 0.30s ease-in-out;
  -ms-transition: all 0.30s ease-in-out;
  -o-transition: all 0.30s ease-in-out;
  outline: none;
  padding: 3px 0px 3px 0px;
  margin: 5px 1px 3px 5rem;
  border: 0;
  border-bottom: 2px solid #1976D2;
  font-size: 12.5px;
  font-weight: 600;
  letter-spacing: 3px;
  /* text-align: center; */
  background: transparent;
  color: #BBDEFB;
}

input[type=text]:focus,
textarea:focus {
  -webkit-box-shadow: 0 0 5px rgba(81, 203, 238, 1);
          box-shadow: 0 0 5px rgba(81, 203, 238, 1);
  padding: 3px 0px 3px 0px;
  outline: 0;
}

::-webkit-input-placeholder {
  color: white;
  opacity: 0.5;
  font-size: 1em;
  font-family: 'Merienda', cursive;
}

:-ms-input-placeholder {
  color: white;
  opacity: 0.5;
  font-size: 1em;
  font-family: 'Merienda', cursive;
}

::-ms-input-placeholder {
  color: white;
  opacity: 0.5;
  font-size: 1em;
  font-family: 'Merienda', cursive;
}

::placeholder {
  color: white;
  opacity: 0.5;
  font-size: 1em;
  font-family: 'Merienda', cursive;
}

@import url('https://fonts.googleapis.com/css?family=Merienda');
body {
  font-family: 'Merienda', cursive;
}


